import { createApolloFetch } from 'apollo-fetch';
import denodeify from 'denodeify';
import { confirm, prompt } from 'node-ask';
import setupDebug from 'debug';
import kill from 'tree-kill';

import { CHROMATIC_INDEX_URL } from '../environment';
import getRuntimeSpecs from './runtimes';
import startApp from './start-app';
import openTunnel from '../lib/tunnel';
import { checkPackageJson, addScriptToPackageJson } from './package-json';
import { getCommit, getBranch } from './git';

const TesterCreateAppTokenMutation = `
  mutation TesterCreateAppTokenMutation($appCode: String!) {
    createAppToken(code: $appCode)
  }
`;

const TesterCreateBuildMutation = `
  mutation TesterCreateBuildMutation($input: CreateBuildInput!, $isolatorUrl: String!) {
    createBuild(input: $input, isolatorUrl: $isolatorUrl) {
      id
      number
      specCount
      componentCount
      webUrl
    }
  }
`;

const TesterBuildQuery = `
  query TesterBuildQuery($buildNumber: Int!) {
    app {
      build(number: $buildNumber) {
        id
        status
        inProgressCount: snapshotCount(status: SNAPSHOT_IN_PROGRESS)
        specCount
        changeCount: snapshotCount(change: SNAPSHOT_VISUALLY_DIFFERENT)
        errorCount: snapshotCount(status: SNAPSHOT_CAPTURE_ERROR)
      }
    }
  }
`;

const debug = setupDebug('tester');

function log(msg) {
  // eslint-disable-next-line no-console
  console.log(`Chromatic Tester: ${msg}`);
}

function pluralize(n, noun, noNumber) {
  const pluralizedNoun = n === 1 ? noun : `${noun}s`;

  return noNumber ? pluralizedNoun : `${n} ${pluralizedNoun}`;
}

async function runQuery(apolloFetch, query, variables) {
  const { data, errors } = await apolloFetch({ query, variables });
  if (errors) {
    throw errors;
  }
  return data;
}

let lastInProgressCount;
async function waitForBuild(apolloFetch, variables) {
  const { app: { build } } = await runQuery(apolloFetch, TesterBuildQuery, variables);
  debug(`build:${JSON.stringify(build)}`);
  const { status, inProgressCount, specCount, changeCount, errorCount } = build;
  if (status === 'BUILD_IN_PROGRESS') {
    if (inProgressCount !== lastInProgressCount) {
      lastInProgressCount = inProgressCount;
      log(
        `${inProgressCount}/${pluralize(specCount, 'spec')} remain to test. ` +
          `(${pluralize(changeCount, 'change')}, ${pluralize(errorCount, 'error')})`
      );
    }

    await new Promise(resolve => setTimeout(resolve, 1000));
    return waitForBuild(apolloFetch, variables);
  }
  return build;
}

export default async function runTest({
  appCode,
  scriptName,
  port,
  appPath = '/',
  indexUrl = CHROMATIC_INDEX_URL,
  createTunnel = true,
}) {
  const uri = `${indexUrl}/graphql`;
  const apolloFetch = createApolloFetch({ uri });

  if (!appCode) {
    throw new Error(
      'You must provide an app code  -- visit https://chromaticqa.com to get your code'
    );
  }

  if (!scriptName || !port) {
    throw new Error(
      'You must provide a npm script name (`--script-name`) and port (`--port`) so we can start your app'
    );
  }

  let token;
  try {
    const { createAppToken } = await runQuery(apolloFetch, TesterCreateAppTokenMutation, {
      appCode,
    });
    token = createAppToken;
  } catch (errors) {
    if (errors[0] && errors[0].message && errors[0].message.match('No app with code')) {
      throw new Error(
        `Incorrect app code '${appCode}' -- visit https://chromaticqa.com to get your code`
      );
    }
    throw errors;
  }

  apolloFetch.use(({ options }, next) => {
    // eslint-disable-next-line no-param-reassign
    options.headers = {
      ...options.headers,
      authorization: `bearer ${token}`,
    };

    next();
  });

  const commit = await getCommit();
  const branch = await getBranch();

  const appPathWithSlash = appPath[0] === '/' ? appPath : `/${appPath}`;
  const url = `http://localhost:${port}${appPathWithSlash}`;
  if (scriptName !== 'none') {
    log(`Starting app with \`npm run ${scriptName}\``);
  }
  const child = await startApp({ scriptName, url });
  if (child) {
    log(`Started app on port ${port}`);
  } else {
    log(`Detected app on port ${port}`);
  }

  const runtimeSpecs = await getRuntimeSpecs(url);

  let isolatorUrl = url;
  let tunnel;
  if (createTunnel) {
    tunnel = await openTunnel({ port });
    log(`Opened tunnel to ${tunnel.url}`);
    isolatorUrl = `${tunnel.url}${appPathWithSlash}`;
  }

  let exitCode = 5;
  try {
    const {
      createBuild: { number, specCount, componentCount, webUrl },
    } = await runQuery(apolloFetch, TesterCreateBuildMutation, {
      input: {
        commit,
        branch,
        runtimeSpecs,
      },
      isolatorUrl,
    });

    const onlineHint = `View it online at ${webUrl}`;
    log(
      `Started Build ${number} ` +
        `(${pluralize(componentCount, 'component')}, ${pluralize(specCount, 'spec')}).

${onlineHint}.`
    );

    const { status, changeCount, errorCount } = await waitForBuild(apolloFetch, {
      buildNumber: number,
    });

    switch (status) {
      case 'BUILD_PASSED':
        log(`Build ${number} passed! ${onlineHint}.`);
        exitCode = 0;
        break;
      // They may have sneakily looked at the build while we were waiting
      case 'BUILD_PENDING':
      case 'BUILD_ACCEPTED':
      case 'BUILD_DENIED':
        log(`Build ${number} has ${pluralize(changeCount, 'change')}. ${onlineHint}.`);
        exitCode = 1;
        break;
      case 'BUILD_FAILED':
        log(`Build ${number} has ${pluralize(errorCount, 'error')}. ${onlineHint}.`);
        exitCode = 2;
        break;
      case 'BUILD_TIMED_OUT':
        log(
          `Build ${number} has timed out. Ensure your machine is connected to the internet and please try again.`
        );
        exitCode = 3;
        break;
      case 'BUILD_ERROR':
        log(`Build ${number} has failed to run. Our apologies. Please try again.`);
        exitCode = 4;
        break;
      default:
        throw new Error(`Unexpected build status: ${status}`);
    }
  } catch (e) {
    if (e.length && e[0] && e[0].message.match(/Cannot run a build with no specs./)) {
      log(e[0].message);
      exitCode = 255;
    } else {
      throw e;
    }
  } finally {
    if (tunnel) {
      tunnel.close();
    }
    if (child) {
      await denodeify(kill)(child.pid, 'SIGHUP');
    }
  }

  if (!checkPackageJson()) {
    let scriptCommand = `chromatic test --app-code '${appCode}' --port=${port}`;

    const confirmed = await confirm(
      "\nYou have not added Chromatic's test script to your `package.json`. Would you like me to do it for you?"
    );
    if (confirmed) {
      let fullScriptName = scriptName;
      if (fullScriptName === 'none') {
        fullScriptName =
          (await prompt('What npm script do you use to start your app? [start]')) || 'start';
      }

      scriptCommand = `${scriptCommand} --script-name='${fullScriptName}'`;

      addScriptToPackageJson('chromatic', scriptCommand);
      console.log(
        '\nAdded script `chromatic`. You can now run it here or in CI with `npm run chromatic` (or `yarn chromatic`)'
      );
    } else {
      const message = `\nNo problem. You can add it later as: "${scriptCommand}`;

      if (scriptName === 'none') {
        console.log(
          `${message} --script-name=<script-name>\n  (<script-name> is the name of the npm script you use to start your app)`
        );
      } else {
        console.log(message);
      }
    }
  }

  return exitCode;
}



